.header {
  width: 100%;
  height: 10vh;
  margin: 0;
  background: rgb(20, 0, 20);
  background: linear-gradient(
    0deg,
    rgba(20, 0, 20, 1) 35%,
    rgba(71, 0, 71, 1) 100%
  );
  &__title {
    font-family: "Bebas Neue", cursive;
    font-size: 60px;
    width: 300px;
    margin-left: 20px;
    color: white;
  }
}
.form__menu--list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 15px;
}
.flix {
  color: #a02b68;
}
